.teacher-dashboard {
  min-height: 100vh;
  position: relative;
}

.dashboard-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.stat-card {
  background: rgba(255, 255, 255, 0.9);
  padding: 25px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-card h3 {
  color: #4a5568;
  font-size: 1.1rem;
  margin-bottom: 10px;
  font-weight: 600;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #4299e1;
}

.main-actions {
  text-align: center;
  margin-bottom: 40px;
}

.create-poll-btn {
  background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(66, 153, 225, 0.3);
}

.create-poll-btn:hover:not(.disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(66, 153, 225, 0.4);
}

.create-poll-btn.disabled {
  background: #cbd5e0;
  cursor: not-allowed;
  box-shadow: none;
}

.current-poll-section {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.current-poll-section h2 {
  color: #2d3748;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.poll-info {
  margin-bottom: 20px;
}

.poll-info h3 {
  color: #4a5568;
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.poll-info p {
  color: #718096;
  margin-bottom: 5px;
}

.poll-results {
  margin-top: 20px;
}

.poll-results h3 {
  color: #2d3748;
  margin-bottom: 15px;
}

.result-item {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  padding: 15px;
  background: #f7fafc;
  border-radius: 8px;
}

.option {
  flex: 1;
  font-weight: 600;
  color: #4a5568;
}

.count {
  font-weight: 600;
  color: #4299e1;
  min-width: 80px;
  text-align: right;
}

.progress-bar {
  flex: 2;
  height: 8px;
  background: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #4299e1, #3182ce);
  transition: width 0.3s ease;
}

.students-section {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.students-section h2 {
  color: #2d3748;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.students-list {
  display: grid;
  gap: 10px;
}

.student-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  background: #f7fafc;
  border-radius: 8px;
  transition: background 0.3s ease;
}

.student-item:hover {
  background: #edf2f7;
}

.student-name {
  font-weight: 600;
  color: #4a5568;
}

.student-status {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.student-status.answered {
  background: #c6f6d5;
  color: #22543d;
}

.student-status.pending {
  background: #fed7d7;
  color: #742a2a;
}

.kick-btn {
  background: #e53e3e;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: background 0.3s ease;
}

.kick-btn:hover {
  background: #c53030;
}

.no-students {
  text-align: center;
  color: #718096;
  font-style: italic;
  padding: 20px;
}

@media (max-width: 768px) {
  .dashboard-stats {
    grid-template-columns: 1fr;
  }
  
  .stat-card {
    padding: 20px;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .current-poll-section,
  .students-section {
    padding: 20px;
  }
  
  .result-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .progress-bar {
    width: 100%;
  }
  
  .student-item {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }
} 