.student-dashboard {
  min-height: 100vh;
  position: relative;
}

.waiting-section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.waiting-message {
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.waiting-message h2 {
  color: #2d3748;
  font-size: 2rem;
  margin-bottom: 15px;
}

.waiting-message p {
  color: #718096;
  font-size: 1.1rem;
}

.poll-section {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.poll-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid #e2e8f0;
}

.poll-header h2 {
  color: #2d3748;
  font-size: 1.8rem;
  font-weight: 700;
}

.timer {
  background: linear-gradient(135deg, #4299e1, #3182ce);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  font-size: 1.1rem;
  box-shadow: 0 2px 10px rgba(66, 153, 225, 0.3);
}

.poll-question {
  margin-bottom: 30px;
}

.poll-question h3 {
  color: #2d3748;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.4;
}

.poll-options {
  margin-bottom: 30px;
}

.poll-options h4 {
  color: #4a5568;
  font-size: 1.2rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.option-label {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  margin-bottom: 10px;
  background: #f7fafc;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.option-label:hover {
  background: #edf2f7;
  border-color: #4299e1;
  transform: translateX(5px);
}

.option-label input[type="radio"] {
  width: 20px;
  height: 20px;
  accent-color: #4299e1;
}

.option-text {
  font-size: 1.1rem;
  font-weight: 500;
  color: #4a5568;
  flex: 1;
}

.submit-answer-btn {
  background: linear-gradient(135deg, #48bb78, #38a169);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(72, 187, 120, 0.3);
  margin-top: 20px;
}

.submit-answer-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(72, 187, 120, 0.4);
}

.submit-answer-btn:disabled {
  background: #cbd5e0;
  cursor: not-allowed;
  box-shadow: none;
}

.poll-results {
  margin-top: 30px;
  padding-top: 25px;
  border-top: 2px solid #e2e8f0;
}

.poll-results h3 {
  color: #2d3748;
  font-size: 1.5rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.total-responses {
  color: #718096;
  font-size: 1.1rem;
  margin-bottom: 20px;
  font-weight: 500;
}

.result-item {
  margin-bottom: 20px;
  padding: 15px;
  background: #f7fafc;
  border-radius: 10px;
  border: 2px solid transparent;
  transition: all 0.3s ease;
  position: relative;
}

.result-item:hover {
  background: #edf2f7;
}

.result-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.option {
  font-weight: 600;
  color: #4a5568;
  font-size: 1.1rem;
}

.count {
  font-weight: 600;
  color: #4299e1;
  font-size: 1.1rem;
}

.progress-bar {
  height: 10px;
  background: #e2e8f0;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 5px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #4299e1, #3182ce);
  transition: width 0.5s ease;
}

.your-answer {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #48bb78;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
}

.poll-complete {
  text-align: center;
  padding: 30px;
  background: #f0fff4;
  border-radius: 10px;
  border: 2px solid #c6f6d5;
  margin-top: 20px;
}

.poll-complete h3 {
  color: #22543d;
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.poll-complete p {
  color: #38a169;
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .poll-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .poll-header h2 {
    font-size: 1.5rem;
  }
  
  .poll-section {
    padding: 20px;
  }
  
  .poll-question h3 {
    font-size: 1.3rem;
  }
  
  .option-label {
    padding: 12px;
  }
  
  .option-text {
    font-size: 1rem;
  }
  
  .result-header {
    flex-direction: column;
    gap: 5px;
    text-align: center;
  }
  
  .your-answer {
    position: static;
    display: inline-block;
    margin-top: 10px;
  }
} 