.login-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.login-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 500px;
  width: 100%;
  animation: slideUp 0.6s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-title {
  color: #2d3748;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 30px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.role-selection h2,
.name-input h2 {
  color: #4a5568;
  font-size: 1.5rem;
  margin-bottom: 30px;
  font-weight: 600;
}

.role-buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.role-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 20px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.1rem;
  font-weight: 600;
}

.role-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.teacher-btn:hover {
  border-color: #4299e1;
  background: #ebf8ff;
}

.student-btn:hover {
  border-color: #48bb78;
  background: #f0fff4;
}

.role-icon {
  font-size: 2rem;
}

.role-text {
  color: #4a5568;
}

.name-input {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.name-input-field {
  width: 100%;
  padding: 15px;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1.1rem;
  margin-bottom: 20px;
  transition: border-color 0.3s ease;
}

.name-input-field:focus {
  outline: none;
  border-color: #4299e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.1);
}

.name-buttons {
  display: flex;
  gap: 15px;
}

.join-btn,
.back-btn {
  flex: 1;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.join-btn {
  background: #4299e1;
  color: white;
}

.join-btn:hover {
  background: #3182ce;
  transform: translateY(-1px);
}

.back-btn {
  background: #e2e8f0;
  color: #4a5568;
}

.back-btn:hover {
  background: #cbd5e0;
}

.error-message {
  background: #fed7d7;
  color: #c53030;
  padding: 12px 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid #feb2b2;
  font-weight: 500;
}

@media (max-width: 768px) {
  .login-card {
    padding: 30px 20px;
  }
  
  .login-title {
    font-size: 2rem;
  }
  
  .role-btn {
    padding: 15px;
    font-size: 1rem;
  }
  
  .name-buttons {
    flex-direction: column;
  }
} 