.poll-history {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.poll-history h2 {
  color: #2d3748;
  font-size: 1.8rem;
  margin-bottom: 30px;
  font-weight: 700;
  text-align: center;
}

.no-history {
  text-align: center;
  color: #718096;
  font-style: italic;
  padding: 40px;
  font-size: 1.1rem;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.history-item {
  background: #f7fafc;
  padding: 25px;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
}

.history-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  border-color: #4299e1;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 2px solid #e2e8f0;
}

.history-header h3 {
  color: #2d3748;
  font-size: 1.3rem;
  font-weight: 600;
  flex: 1;
  margin-right: 20px;
}

.history-date {
  color: #718096;
  font-size: 0.9rem;
  font-weight: 500;
  white-space: nowrap;
}

.history-stats {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.stat {
  background: #edf2f7;
  color: #4a5568;
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.stat.winner {
  background: #c6f6d5;
  color: #22543d;
}

.history-results {
  margin-top: 20px;
}

.history-results h4 {
  color: #2d3748;
  font-size: 1.1rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.history-result-item {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  padding: 15px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  position: relative;
  transition: all 0.3s ease;
}

.history-result-item:hover {
  background: #f7fafc;
  border-color: #4299e1;
}

.result-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
  margin-bottom: 10px;
}

.option {
  font-weight: 600;
  color: #4a5568;
  font-size: 1rem;
}

.count {
  font-weight: 600;
  color: #4299e1;
  font-size: 1rem;
}

.progress-bar {
  flex: 2;
  height: 8px;
  background: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #4299e1, #3182ce);
  transition: width 0.5s ease;
}

.winner-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.2rem;
  animation: bounce 1s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  60% {
    transform: translateY(-3px);
  }
}

@media (max-width: 768px) {
  .poll-history {
    padding: 20px;
  }
  
  .poll-history h2 {
    font-size: 1.5rem;
  }
  
  .history-header {
    flex-direction: column;
    gap: 10px;
  }
  
  .history-header h3 {
    margin-right: 0;
    font-size: 1.2rem;
  }
  
  .history-stats {
    flex-direction: column;
    gap: 10px;
  }
  
  .history-result-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .result-header {
    width: 100%;
  }
  
  .progress-bar {
    width: 100%;
  }
  
  .winner-badge {
    position: static;
    display: inline-block;
    margin-top: 10px;
  }
} 